import { Config } from 'Config';

import { View as BaseView } from 'curvature/base/View';

export class Head extends BaseView
{
	static get()
	{
		return this.instance = this.instance || new this;
	}

	constructor(args = {})
	{
		super(args);

		args.title = args.title || Config.title;
		args.description = args.description || '';

		const tag = document.head;

		args.bindTo('title', (v) => {

			document.title = v;

			const metaTitle = this.getTag('meta', {name:'title'});
			const ogTitle = this.getTag('meta', {property:'og:title'});

			metaTitle.setAttribute('content', v);
			ogTitle.setAttribute('content', v);

			console.log(metaTitle);
		});

		args.bindTo('description', (v) => {
			const metaDesc = this.getTag('meta', {name:'description'});
			const ogDesc = this.getTag('meta', {property:'og:description'});

			metaDesc.setAttribute('content', v);
			ogDesc.setAttribute('content', v);			
		});

		args.bindTo('status', (v) => {
			const metaDesc = this.getTag('meta', {name:'prerender-status-code'});
			const ogDesc = this.getTag('meta', {'http-equiv':'Status'});

			metaDesc.setAttribute('content', v);
			ogDesc.setAttribute('content', v);			
		});

		this.template = require('./head.tmp');

		this.tags = {};
	}

	getTag(name, attributes = {})
	{
		const keys = Object.keys(attributes).sort();
		const attr = keys.map(k=>`[${k}='${attributes[k]}']`)

		const selector = name + attr;

		if(this.tags[selector])
		{
			return this.tags[selector];
		}		

		const existing = document.querySelector(selector);

		if(existing)
		{
			return existing;
		}

		const newTag = this.tags[selector] = document.createElement(name);
		
		console.log(selector);

		document.head.append(newTag);

		keys.map(k => newTag.setAttribute(k, attributes[k]));

		return newTag;
	}
}
